"use client";

import { useState } from "react";
import { motion } from "framer-motion";
import { Search } from "lucide-react";

interface PublicInputProps {
    onScan: (username: string) => void;
}

export function PublicInput({ onScan }: PublicInputProps) {
    const [username, setUsername] = useState("");

    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault();
        if (username.trim()) {
            onScan(username);
        }
    };

    return (
        <motion.form
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            onSubmit={handleSubmit}
            className="w-full max-w-2xl mx-auto space-y-6"
        >
            <div className="relative group">
                <div className="absolute inset-y-0 left-4 flex items-center pointer-events-none">
                    <span className="text-neon-red font-mono text-xl">@</span>
                </div>
                <input
                    type="text"
                    value={username}
                    onChange={(e) => setUsername(e.target.value)}
                    placeholder="Enter his username..."
                    className="w-full bg-black/50 border-2 border-gray-700 rounded-xl py-6 pl-12 pr-4 text-xl font-mono text-white placeholder-gray-600 focus:border-neon-red focus:outline-none focus:shadow-[0_0_20px_rgba(220,38,38,0.2)] transition-all duration-300"
                />
                <div className="absolute inset-y-0 right-4 flex items-center">
                    <Search className="w-6 h-6 text-gray-600 group-focus-within:text-neon-red transition-colors" />
                </div>
            </div>

            <button
                type="submit"
                disabled={!username.trim()}
                className="w-full bg-neon-red text-black font-black text-xl py-6 rounded-xl uppercase tracking-widest hover:bg-white hover:scale-[1.02] active:scale-[0.98] disabled:opacity-50 disabled:hover:scale-100 disabled:hover:bg-neon-red transition-all duration-300 shadow-[0_0_30px_rgba(220,38,38,0.4)] animate-pulse"
            >
                Run Vibe Check
            </button>
        </motion.form>
    );
}
